.info {
  display: flex;
  position: relative;
  @include desktop-hover() {
    svg {
      fill: #01231f;
    }
    .info__hint {
      opacity: 1;
      visibility: visible;
    }
  }
  svg {
    fill: #80918f;
    cursor: pointer;
    transition: 0.3s ease-in-out;
  }
  &__hint {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: calc(100% + 8px);
    display: flex;
    width: 320px;
    padding: 12px;
    border-radius: 8px;
    background: var(--Primary-Main, #01231f);
    color: #fff;
    line-height: 16px;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease-in-out;
    @include response(767px) {
      width: 250px;
    }
    p {
      margin: 0;
    }
    .info.active & {
      opacity: 1;
      visibility: visible;
    }
    &::before {
      content: "";
      position: absolute;
      right: 100%;
      top: 50%;
      margin-top: -6px;
      border: 6px solid transparent;
      border-right: 4px solid var(--Primary-Main, #01231f);
    }
    &--bottom {
      width: 260px;
      left: 50%;
      margin-left: -130px;
      top: calc(100% + 8px);
      transform: none;
      &::before {
        content: "";
        position: absolute;
        border-right: 6px solid transparent;
        border-bottom: 4px solid var(--Primary-Main, #01231f);
        bottom: 100%;
        left: 50%;
        margin-left: -6px;
        right: auto;
        top: auto;
        margin-top: 0;
      }
    }
  }
}
