.search {
  flex: 0 0 520px;
  width: 520px;
  @include response(1200px) {
    width: auto;
    flex: none;
  }
  &__mobile-btn {
    display: none;
    @include response(1200px) {
      display: flex;
    }
  }
  &__mobile-btn-open {
    @include response(1200px) {
      fill: #fff;
      .search.active & {
        display: none;
      }
    }
  }
  &__mobile-btn-close {
    @include response(1200px) {
      display: none;
      .search.active & {
        display: block;
      }
    }
  }
  &__form-holder {
    position: relative;
    @include response(1200px) {
      position: absolute;
      left: 0;
      right: 0;
      top: 100%;
      height: calc(100dvh - 57px);
      background: rgba(0, 0, 0, 0.3);
      z-index: 3;
      opacity: 0;
      visibility: hidden;
      .search.active & {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__form {
    position: relative;
  }
  &__input {
    width: 100%;
    border-radius: 99px;
    border: 1px solid var(--main-30, #b3bdbc);
    background: var(--Primary-White, #fff);
    padding: 4px 24px;
    height: 42px;
    font-family: "Helvetica Neue";
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      border-radius: 0;
      height: 60px;
    }
    &::placeholder {
      color: var(--main-50, #80918f);
      font-weight: 400;
    }
    &:focus {
      border: 1px solid var(--Primary-Accent, #ff3d00);
    }
  }
  &__btn {
    position: absolute;
    background: transparent;
    right: 24px;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0;
    padding: 0;
    @include response(1200px) {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      background: #ff3d00;
      top: 50%;
      margin-top: -16px;
    }
    @include desktop-hover() {
      svg {
        fill: #ff3d00;
      }
    }
    svg {
      fill: #4d6562;
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        fill: #fff;
      }
    }
  }
}
